import React from "react";
import ReactDOM from "react-dom/client";
import BasicExample from "./components/header/App";
import reportWebVitals from "./reportWebVitals";
import FlipTile from "./components/tile/Tile";
import Section from "./components/intro/Intro";
import { Container } from "react-bootstrap";
import Languages from "./components/languages/languages";
import Folder from "./components/folder/Folder";
import "./index.css";
import config from "./config"; // Import the data from config.js
import Footer from "./components/footer/Footer";
import DescriptionSection from "./components/description/DescriptionSection";

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <div>
    <Container className="full-height-container">
      <BasicExample />
      <Section />
      <Folder name="Description">
        <DescriptionSection />
      </Folder>
      <Folder name="Languages">
        <Languages />
      </Folder>
      {config.map((folder, folderIndex) => (
        <Folder key={folderIndex} name={folder.name}>
          {folder.items.map((item, itemIndex) => (
            <FlipTile
              key={itemIndex}
              imageSrc={item.imageSrc}
              backText={item.backText}
            />
          ))}
        </Folder>
      ))}
    </Container>
    <Footer />
  </div>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
