import React, { useState, useRef, useEffect } from "react";
import { Card } from "react-bootstrap";
import "./Tile.css";

const FlipTile = ({ imageSrc, backText }) => {
  const [isEnlarged, setIsEnlarged] = useState(false);
  const [dimensions, setDimensions] = useState({ width: 0, height: 0 });
  const imgRef = useRef(null);

  const handleClick = (e) => {
    e.stopPropagation();
    setIsEnlarged(!isEnlarged); // Toggle between enlarged and original
  };

  useEffect(() => {
    if (imgRef.current) {
      const handleLoad = () => {
        const { width, height } = imgRef.current;
        setDimensions({ width, height });
      };

      imgRef.current.addEventListener("load", handleLoad);

      // Clean up event listener
      return () => {
        imgRef.current.removeEventListener("load", handleLoad);
      };
    }
  }, [imageSrc]); // Re-run if `imageSrc` changes

  return (
    <div
      className={`flip-card ${isEnlarged ? "enlarged" : ""}`}
      onClick={handleClick}
    >
      <div className="flip-card-inner">
        {!isEnlarged ? (
          <div className="flip-card-front">
            <Card>
              <Card.Img
                variant="top"
                src={imageSrc}
                ref={imgRef}
                style={{ maxWidth: "100%" }}
              />
            </Card>
          </div>
        ) : (
          <div className="flip-card-back">
            <Card>
              <Card.Body>
                <Card.Text>{backText}</Card.Text>
              </Card.Body>
            </Card>
          </div>
        )}
      </div>
      {isEnlarged && (
        <button className="close-button" onClick={handleClick}>
          X
        </button>
      )}
    </div>
  );
};

export default FlipTile;
