import React, { useState, useEffect } from "react";
import "./languages.css"; // Import the CSS file for styling

const languageData = [
  {
    name: "Python",
    percentage: 95,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/python/python-original.svg",
  },
  {
    name: "Golang",
    percentage: 70,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/go/go-original.svg",
  },
  {
    name: "Swift",
    percentage: 70,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/swift/swift-original.svg",
  },
  {
    name: "Kotlin",
    percentage: 50,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/kotlin/kotlin-original.svg",
  },
  {
    name: "React.js",
    percentage: 50,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/react/react-original.svg",
  },
  {
    name: "Node.js",
    percentage: 70,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/nodejs/nodejs-original.svg",
  },
  {
    name: "C",
    percentage: 70,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/c/c-original.svg",
  },
  {
    name: "C++",
    percentage: 60,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/cplusplus/cplusplus-original.svg",
  },
  {
    name: "MySQL",
    percentage: 60,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/mysql/mysql-original.svg",
  },
  {
    name: "ElasticSearch",
    percentage: 80,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/elasticsearch/elasticsearch-original.svg",
  },
  {
    name: "Kubernetes",
    percentage: 60,
    imageSrc:
      "https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/kubernetes/kubernetes-original.svg",
  },
];

const Languages = () => {
  return (
    <div className="languages-container">
      {languageData.map((lang, index) => (
        <LanguageBar
          key={index}
          name={lang.name}
          percentage={lang.percentage}
          imageSrc={lang.imageSrc}
        />
      ))}
    </div>
  );
};

const LanguageBar = ({ imageSrc, percentage }) => {
  const [fill, setFill] = useState(0);

  useEffect(() => {
    // Add a slight delay before setting the fill percentage to simulate loading
    const timer = setTimeout(() => {
      setFill(percentage);
    }, 500); // Adjust the delay as needed (in milliseconds)

    return () => clearTimeout(timer);
  }, [percentage]);

  return (
    <div className="language-item">
      <img
        src={imageSrc}
        alt="Programming Language"
        className="language-image"
      />
      <div className="health-bar-container">
        <div className="health-bar">
          <div className="health-bar-fill" style={{ width: `${fill}%` }}></div>
        </div>
      </div>
    </div>
  );
};

export default Languages;
