import React, { useState } from "react";
import "./DescriptionSection.css"; // Import the CSS file
import waterlooImg from "../../photos/waterloo_logo.png"; // Ensure the path is correct

const DescriptionSection = () => {
  const [isSpinning, setIsSpinning] = useState(false);

  const handleImageClick = () => {
    setIsSpinning(!isSpinning); // Toggle the spinning state
  };

  return (
    <div className="description-section">
      <div className="description-content">
        <h1 className="description-title">Hello!  I'm Steven</h1>
        <p className="description-text">
          Hello! My name is Steven Li and I am currently a third-year
          undergraduate studying Computer Engineering at the University of
          Waterloo! I have a keen interest in backend development and
          cybersecurity! If you have any opportunities please let's connect and
          chat!
        </p>
      </div>
      <img
        src={waterlooImg} // Replace with your image source
        alt="University of Waterloo Logo"
        className={`img-fluid ${isSpinning ? "spin" : ""}`}
        onClick={handleImageClick} // Add click handler
      />
    </div>
  );
};

export default DescriptionSection;
